<script>
import axios from "@axios"
import themeConfig from '@themeConfig'
import { ref, onUnmounted } from '@vue/composition-api'
import store from '@/store'
import { mdiClose, mdiCogOutline } from '@mdi/js'
import{ required } from '@core/utils/validation'

export default {

  setup() {
    const isIssueDateMenuOpen = ref(false)
    const isDueDateMenuOpen = ref(false)
    return {
      isIssueDateMenuOpen,
      isDueDateMenuOpen,
      required,

      // themeConfig
      appName: themeConfig.app.name,
      appLogo: themeConfig.app.logo,

      // Icons
      icons: {
        mdiClose,
        mdiCogOutline,
      },
    }
  },
  data(){
    return{
      quota:{
        work_vs_amount: [
          {
            work: {
              title: '',
              description: '',
            },
            unit_price: 0,
            amount: 0,
          },
        ],
        user: '',
        issue_date: '',
        due_date: '',
        customer: [],
      },
      customers: [],
    }
  },
    async created(){
      const { data } = await axios.get('/customer')
      this.customers = data
    },
    methods:{
      addNewItem(){
        this.quota.work_vs_amount.push({
          work: {
            title: '',
            description: '',
          },
          unit_price: 0,
          amount: 0,
        })
      },
      onIssueDateMenuOpen(){
        this.isIssueDateMenuOpen = true
      },
      onDueDateMenuOpen(){
        this.isDueDateMenuOpen = true
      },
      onIssueDateMenuClose(){
        this.isIssueDateMenuOpen = false
      },
      onDueDateMenuClose(){
        this.isDueDateMenuOpen = false
      },
      onIssueDateChange(date){
        this.quota.issue_date = date
      },
      onDueDateChange(date){
        this.quota.due_date = date
      },
      onSubmit(){
        axios.post('/quota/', this.quota)
          .then(
            console.log(this.quota.customer),
            this.$router.push('/apps/quota/list')
          )
          .catch(err => {
            console.log(err)
          })
      },
    },

  }

</script>

